import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AgmCoreModule } from '@agm/core';
import { NglModule } from 'ng-lightning/ng-lightning';

import { AdminDispensariesRoutingModule } from './admin-dispensaries.routing';
import { AdminDispensariesService } from './admin-dispensaries.service';

import { AdminDispensariesComponent } from './main/admin-dispensaries.component';
import { AdminDispensariesAdminComponent } from './admin/admin-dispensaries-admin.component';
import { AdminDispensariesAdminDealsComponent } from './admin/deals/admin-dispensaries-admin-deals.component';
import { AdminDispensariesAdminPermissionsComponent } from './admin/permissions/admin-dispensaries-admin-permissions.component';
import { AdminDispensariesAdminProductsComponent } from './admin/products/admin-dispensaries-admin-products.component';
import { AdminDispensariesAdminProductsAdminStrainsComponent } from './admin/products/strains/admin-dispensaries-admin-products-admin-strains.component';
import { AdminTeamDispensariesComponent } from './team/admin-team-dispensaries.component';
import { AdminLocationsDispensariesComponent } from './locations/admin-locations-dispensaries.component';
import { AdminStorefrontDispensariesComponent } from './panda-storefront/admin-storefront-dispensaries.component';
import { AdminSocialMediaDispensariesComponent } from './social-media/admin-social-media-dispensaries.component';

@NgModule({
  imports: [
    CommonModule,
    AdminDispensariesRoutingModule,
    AgmCoreModule,
    NglModule
  ],
  declarations: [
    AdminDispensariesComponent,
    AdminDispensariesAdminComponent,
    AdminDispensariesAdminDealsComponent,
    AdminDispensariesAdminPermissionsComponent,
    AdminDispensariesAdminProductsComponent,
    AdminDispensariesAdminProductsAdminStrainsComponent,
    AdminTeamDispensariesComponent,
    AdminLocationsDispensariesComponent,
    AdminStorefrontDispensariesComponent,
    AdminSocialMediaDispensariesComponent
  ],
  providers: [
    AdminDispensariesService
  ]
})
export class AdminDispensariesModule {
}



