import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { TradeService } from '../../../../common/trade/trade.service';
import "rxjs/add/operator/takeWhile";

@Component({
  selector: 'admin-storefront-dispensaries',
  templateUrl: './admin-storefront-dispensaries.component.html',
  styleUrls: ['./admin-storefront-dispensaries.component.css']
})
export class AdminStorefrontDispensariesComponent implements OnInit {

  private tradeLocationCode;
  private dispensarie;
  private alive = true;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private tradeService: TradeService
  ) { }

  ngOnInit() {
    this.route.params.takeWhile(() => this.alive)
      .subscribe((params: Params) => {
      });
  }

  ngOnDestroy() {
    this.alive = false;
  }
}



