import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { AdminDispensariesService } from '../../admin-dispensaries.service';
import "rxjs/add/operator/takeWhile";

@Component({
  selector: 'admin-dispensaries-admin-permissions',
  templateUrl: './admin-dispensaries-admin-permissions.component.html',
  styleUrls: ['./admin-dispensaries-admin-permissions.component.css']
})
export class AdminDispensariesAdminPermissionsComponent implements OnInit {

  private alive = true;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private adminDispensariesService: AdminDispensariesService
  ) { }

  ngOnInit() {
    this.route.params.takeWhile(() => this.alive)
      .subscribe((params: Params) => {
        console.log("PERMISSIONS>>>>>>> ", params);
      });
  }

  ngOnDestroy() {
    this.alive = false;
  }
}



