import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { AdminDispensariesService } from '../admin-dispensaries.service';
import "rxjs/add/operator/takeWhile";

@Component({
  selector: 'admin-social-media-dispensaries',
  templateUrl: './admin-social-media-dispensaries.component.html',
  styleUrls: ['./admin-social-media-dispensaries.component.css']
})
export class AdminSocialMediaDispensariesComponent implements OnInit {

  private alive = true;
  private links: any[];

  constructor(
    private route: ActivatedRoute,
    private router: Router
  ) { }

  ngOnInit() {
    this.route.params.takeWhile(() => this.alive)
      .subscribe((params: Params) => {
      });

    this.links = [
      { icon: "fa-facebook", link: "facebook.com", selected: false },
      { icon: "fa-twitter", link: "twitter.com", selected: true },
      { icon: "fa-instagram", link: "instagram.com", selected: false },
    ]
  }

  ngOnDestroy() {
    this.alive = false;
  }
}



