import { Injectable } from '@angular/core';
import { TradeService } from '../../../common/trade/trade.service';

@Injectable()
export class AdminDispensariesService {

  private dispensarie;
  private tradeLocationCode;
  private alive = true;

  constructor(private tradeService: TradeService) {
  }

  public setDispensarie(tradeLocationCode) {
    this.tradeService.getTrade(tradeLocationCode).takeWhile(() => this.alive)
      .subscribe((dispensarie) => {
        this.dispensarie = dispensarie;
        console.log("setDispensarie>>> ", this.dispensarie);
      });
  }

  ngOnDestroy() {
    this.alive = false;
  }
}



