import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { AdminDispensariesService } from '../admin-dispensaries.service';
import "rxjs/add/operator/takeWhile";

@Component({
  selector: 'admin-dispensaries',
  templateUrl: './admin-dispensaries.component.html',
  styleUrls: ['./admin-dispensaries.component.css']
})
export class AdminDispensariesComponent implements OnInit {

  private alive = true;
  private menuList: [{ title: string, path: string }];

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private adminDispensariesService: AdminDispensariesService
  ) {

    this.menuList = [
      { title: "Admin", path: "admin" },
      { title: "Team Members", path: "team" },
      { title: "Locations", path: "locations" },
      { title: "Social Media", path: "social-media" },
      { title: "Panda Storefront", path: "panda-storefront" }
    ]
  }

  ngOnInit() {
    this.route.params.takeWhile(() => this.alive)
      .subscribe((params: Params) => {
        this.adminDispensariesService.setDispensarie(params['id']);
      });
  }

  ngOnDestroy() {
    this.alive = false;
  }
}



